<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process19"></a>

<p><br></p>
<p><span class="rvts17">腥红王子（The Red Prince）:</span></p>
<p><img width="380" height="679" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber63.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">腥红王子是蜥蜴族中最独一无二的王子，由于被诬陷与恶魔有染，他却被自己的帝国所驱逐。在苍茫的大地上，面对着卑微的居民，谁也无法阻止他的洗冤之行。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族：蜥蜴</span>
<br><span class="rvts21">默认职业：战士（</span><span class="rvts20">Fighter</span><span class="rvts21">）</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族天赋：</span>
<br><span class="rvts21">腥红王子拥有蜥蜴族的起始天赋。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特有标签：</span>
<br><span class="rvts21">腥红王子只能被选为男性，并附带腥红王子特有标签。该标签可以使得他拥有一些特定的对话和行动。另外她还具备学者和贵族的标签。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="143" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="236" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="143" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="630" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="143" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber64.jpg"></p>
</td>
<td width="236" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Dragon's Blaze</span></p>
</td>
<td width="143" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">火焰龙熄</span></p>
</td>
<td width="630" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">吐出火焰，对一条线敌人造成燃烧效果，</span>
<br><span class="rvts21">并使地面成为火元素类型，持续</span><span class="rvts20">2</span><span class="rvts21">轮</span></p>
</td>
</tr>
<tr valign="top">
<td width="143" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber65.jpg"></p>
</td>
<td width="236" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Domestic Stare</span></p>
</td>
<td width="143" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">贵族凝视</span></p>
</td>
<td width="630" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">吸取目标身上的魔法护甲成为己有</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts48"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts20">1. </span><span class="rvts21">腥红王子具备蜥蜴族可以挖土的优势，比较实用。</span>
<br><span class="rvts20">2. </span><span class="rvts21">另外，拥有可以汲取敌人魔法护甲成为己有，腥红王子更适合战法职业。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



